.chat {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}

.messages {
  display: flex;
  flex-direction: column;
}

.messenger {
  display: flex;
  flex-direction: column-reverse;
  padding: 5px;
  width: 350px;
  height: 500px;
  background-color: #e3b7c4;
  opacity: 1;
  background-image: radial-gradient(circle, #f359a7 10%, transparent 11%),
    radial-gradient(circle at bottom left, #f359a7 5%, transparent 6%),
    radial-gradient(circle at bottom right, #f359a7 5%, transparent 6%),
    radial-gradient(circle at top left, #f359a7 5%, transparent 6%),
    radial-gradient(circle at top right, #f359a7 5%, transparent 6%);
  background-size: 31px 31px;
  overflow-x: hidden;
  overflow-y: scroll;
}

.message {
  display: flex;
}

.selfMessage {
  display: block;
  padding: 5px 10px;
  margin: 5px 0 0 0;
  background: #00b4d8;
  border-radius: 10px 10px 0 10px;
  margin-right: 0;
  margin-left: auto;
  color: snow;
  max-width: 70%;
  transition: 0.5s;
}

.botMessage {
  display: flex;
  padding: 5px;
  margin: 5px 0 0 0;
  background: #8a39e1;
  border-radius: 10px 10px 10px 0;
  margin-left: 0;
  margin-right: auto;
  color: #ffe61b;
  max-width: 70%;
  transition: 0.5s;
}

.messageImg {
  width: 83%;
  margin-top: 0;
  border-radius: 5px;
  cursor: pointer;
}

.sticker {
  height: 50px;
  width: 36.75px !important;
  margin-right: 5px;
  cursor: none;
}

.self-message,
.bot-message {
  font-weight: bold;
}

::selection {
  color: none;
  background: none;
}

/* Клавиатура */
.keyboard {
  padding: 0 5px;
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;
}

.key {
  background: transparent;
  color: snow;
  border: 3px solid snow;
  border-radius: 5px;
  padding: 5px 10px;
  font-weight: bold;
  cursor: pointer;
  transition: 0.5s;
  margin: 5px;
}

.keyboard .key:hover {
  color: #000;
  background: pink;
  border: 3px solid #000;
}

/* Скроллбар и кнопки скролла */
::-webkit-scrollbar {
  width: 7px;
  height: 7px;
}
::-webkit-scrollbar-button {
  width: 0px;
  height: 0px;
}
::-webkit-scrollbar-thumb {
  background: #de2c85;
}
::-webkit-scrollbar-track {
  background: #f359a7;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
